import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import axios from 'axios'

import { API_URL, BUCKET } from '../utils/Variables'

import '../styles/Shop.css'

const Shop = () => {
    const [products, setProducts] = useState([])
    const [loading, setLoading] = useState(false)

    useEffect(() => {
        console.log("Using Effect - Shop.js")

        setLoading(true)

        axios.get(`${API_URL}/products`).then(response => {
            setProducts(response.data)
        }).catch(error => console.log(error))

        setLoading(false)
    }, [])

    return(
        <div>
            

            {/* show error message if API response isn't legit */}
            { Array.isArray(products) ?
               <h1>products</h1> :
               <h1>Invalid response from API!</h1> 
            }

            <div className="ProductsContainer">
                { loading && <h3>fetching products</h3> }
                { Array.isArray(products) && products.map(product => (
                    <Link 
                        to={`/shop/${product.slug}`}
                        key={product.slug}
                        className='ProductThumbnail'
                    >
                        <img className="ProductImage" src={`${BUCKET}/product_images/${product.slug}/${product.images[0]}`} alt='product' />
                        <p>${product.price}</p>
                        <h1>{product.name}</h1>
                        <p>{product.description}</p>
                    </Link>
                ))}
            </div>
        </div>

    )
}

export default Shop